 

import utility from './utility'
import dateforamt from 'dateformat' 
import { mapActions } from 'vuex'
import countries from './country'
import CountryFlag from 'vue-country-flag'
export default {
    install(Vue) {
        Vue.prototype.$getNestedValue = utility.getObjectValueByPath.bind(utility)
        Vue.mixin({
            components: {
                CountryFlag
            },
            methods: {
                ...mapActions({
                    get: 'api/get',
                    save: 'api/save',
                    update: 'api/update',
                    remove: 'api/remove',
                    getItem: 'api/getById',
                }),
                getFormatArgs(args, item) {
                    if (args)
                        args = args.map(el => {
                            if (typeof el == 'string' && el.substr(0, 1) == '$')
                                return this.$getNestedValue(item, el.substr(1, el.length))
                            else
                                return el
                        })
                    // console.log(args);
                    return args || []
                }
            },
            computed: {
                user () {
                    return this.$store.state.auth.user
                }
            },
            filters: {
                dynamic (value, filterName, args) {
                    if (filterName)
                        return Vue.filter(filterName)(value, ...args)
                    else 
                        return value || '-'
                },
                country(value, flag = true, name = true, scale = 0.5) {
                    const country = countries.filter(e => e.code == value)
                    if (country.length) {
                        if (flag) {
                            return `<small class=" text-center d-flex" style="width:auto"><span class="flag f-${value.toLowerCase()} normal-flag" style="
                                        transform: scale(${scale});
                                        margin: -10px -15px;
                                    "></span>
                                    ${
                                        name ? `<span class="align-self-center">${country[0].name}</span>` : ''
                                    }
                                    
                                    
                                    </small>`
                        } else
                            return country[0].name
                    }
                    else
                        return value

                },
                money (value, currency = 'USD', removeZero = false) {
                    const isZero = Number(value) == 0
                    const negative = Number(value) < 0
                    const _class = negative ? 'red--text' : 'green--text'

                    if (removeZero && isZero) {
                        return '-'
                    } else
                        return `<span class="${_class}">${utility.money(value, currency)}</span>`
                },
                date(value, format = 'dd/mm/yyyy') {
                    return value ? dateforamt(value, format) : '-'
                },
                depositType (value) {
                    return `<i aria-hidden="true" class="v-icon notranslate mdi mdi-bank-transfer-${value == 'wtd' ? 'out' : 'in'}  ${value == 'wtd' ? 'red' : 'green'}--text"></i>`
                }
            }
        })
    }

}

